<template>
  <div class="input">
    <input v-model="value" />
  </div>
</template>

<script>
export default {
  name: "Input",
  props: ["modelValue"],
  computed: {
    value: {
      get() {
        return this.modelValue;
      },
      set(value) {
        console.log("asd");
        this.$emit("input", value);
      },
    },
  },
};
</script>

